body {
  margin: 0;
  font-family: "Avenir Medium", -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  overflow: hidden;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

.menu {
  position: absolute;
  top: 0;
  left: 0;
  z-index: 1;
  border-radius: 48px 0 0 0;
  background-color: #1F1B33;
  width: 100vw;
  height: 100vh;
}

.menu .name {
  position: absolute;
  top: 122px;
  left: 58px;
  font-size: 28px;
  font-family: "Avenir Black", sans-serif;
  color: white;
}

.menu-items {
  position: absolute;
  top: 160px;
  left: 27px;
  width: 133px;
  margin: 57px 0 60px;
}

.menu-item {
  width: 100%;
  border-radius: 15px;
  height: 51px;
  line-height: 51px;
  color: white;
  font-size: 20px;
  text-align: left;
  padding-left: 27px;
  margin-bottom: 16px;

}

.menu-item.active {
  color: #FC8074;
  background-color: rgba(252,128,116, 0.2);
}

.sign-out {
  color: white;
  text-align: left;
  border-top: 1px solid #84818E;
  font-size: 20px;
  padding: 60px 0 0 27px;
  width: 160px;
  margin-top: 42px;
}

.content {
  position: relative;
  width: 100vw;
  height: 100vh;
  z-index: 2;
  background-color: white;
  border-radius: 46px 0 0 0;
  transition: transform 0.3s ease-in;
  transform: unset;
}

.content.open {
  transform: rotate(-7deg) translate(240px) translateY(39px);
}

.content .menu-logo {
  position: absolute;
  top: 43px;
  left: 36px;
}

.content .title {
  position: absolute;
  top: 43px;
  left: 101px;
  font-size: 24px;
  color: #9F9FA0;
  line-height: 33px;
  letter-spacing: 5px;
}

.App {
  text-align: center;
}

.App-logo {
  height: 40vmin;
  pointer-events: none;
}

@media (prefers-reduced-motion: no-preference) {
  .App-logo {
    -webkit-animation: App-logo-spin infinite 20s linear;
            animation: App-logo-spin infinite 20s linear;
  }
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

@-webkit-keyframes App-logo-spin {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}

@keyframes App-logo-spin {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}

